/**
 * 管理员用户配置文件
 * 这里配置的OpenID将拥有管理员权限
 */

// 管理员用户的OpenID列表
const adminOpenIds = [
  // 在这里添加管理员用户的OpenID
  // 例如: 'oxxx1234567890abcdef',
];

// 检查用户是否为管理员
export function isAdminUser(openid) {
  return adminOpenIds.includes(openid);
}

export default {
  adminOpenIds,
  isAdminUser
}; 